
import {defineComponent, ref, Ref, watch, onMounted} from 'vue';
import {useCookies} from '@vueuse/integrations/useCookies'
import Ability from "@/components/elements/Ability.vue";
import Data from '@/ability.json';
import {Ability as AbilityType, Effects} from '@/types/Ability'

export default defineComponent({
    components: {Ability},
    setup() {
        const favorites: Ref<Array<string>> = ref([]);
        const favoriteItems: Ref<AbilityType[]> = ref([]);
        const cookies = useCookies();
        const effects: Effects = {
            'attack-up': 'Увеличение <b>следующей</b> атаки на 20%. Эффекты увеличения атаки можно суммировать.',
            'attack-down': 'Снижение урона от следующей атаки на 20%. Эффект работает <b>только на следующую атаку</b>. Эффект может суммироваться.',
            'speed-up': 'Увеличение скорости на 20%, на 1 или 2 раунда (в зависимости от карты). Эффекты увеличения скорости можно суммировать.',
            'speed-down': 'Снижение скорости на 20% на 2 раунда – текущий и следующий . Эффекты снижения скорости суммируются.',
            'morale-up': 'Увеличение морали на <b>следующий раунд</b> на 20%. Эффекты увеличения морали можно суммировать.',
            'morale-down': 'Снижение морали на 20% на 2 раунда – текущий и следующий. Эффекты снижения морали суммируются.',
            'aroma': 'Акси, на котором есть эффект Aroma будет приоритетной целью для противника. Атака всегда будет направлена на Аксика, на котором висит Aroma. Эффект работает только в текущем раунде.',
            'chill': 'Axie, пораженный дебаффом Chill, не может войти в режим <b>Last Stand</b>. Эффект работает в текущем и следующем раунде.',
            'fear': 'После поражения Fear эффектом, Акси может атаковать, но промахивается. Эффект работает по-разному в зависимости от карты с Fear эффектом – на 1 или 2 атаки.',
            'fragile': 'Щит Акси, пораженного Fragile, получает двойной урон во время следующей входящей атаки. Эффект переносится на следующий раунд, если акси не получил входящий урон в текущем раунде.',
            'jinx': 'Пораженный эффектом Jinx не сможет нанести критический удар в текущем и в следующем раунде.',
            'lethal': 'Следующий удар по Акси нанесет критечский урон. Эффект действует <b>до следующей атаки по пораженному Акси</b>.',
            'poison': 'Акси под эффектом яда будет получать по 2 урона <b>за каждое действие любого игрока</b>. Эффекты яда суммируются.',
            'sleep': 'Следующая входящая атака обойдет щит, урон придется сразу по HP Axie. Эффект действует до следующей атаки.',
            'stench': 'Другие Акси <b>не будут</b> атаковать питомца, пораженного дебаффом Stench. Действует только в текущем раунде.',
            'stun': 'Акси, пораженный эффектом Stun промахивается по цели следующей атакой. Еще один очень важный момент – следующая <b>входящая</b> атака по Акси, на котором есть Stun, <b>проигнорирует щит</b>. Эффект действует <b>до следующей атаки</b>.',
            'prioritize': 'Таргетинг по определенным существам.',
            'untargetable': 'Перенаправляет таргетинг от себя',
            'remove-debuff': 'Очищение от дебаффов.',
            'gain-energy': 'Экономический эффект, в основном это добор энергии.',
            'disable-ability': 'Отключить карты, в основном отключает карты противника определенного класса.',
            'end-last-stand': 'Отключает состояние last stand.',
            'raise-damage': 'Увеличение урона за счет чего-то.',
            'discard': 'Выкинуть карту у врага или у себя, в зависимости от эффекта.',
            'damage-reflect': 'Отражение урона наносящим противником.',
            'double-hit': 'Атакует дава раза текущей картой.',
            'multi-hit': 'Атакует более двух раз текущей картой.',
            'self-heal': 'Лечения текущего Акси на определенное кол-во здоровья, в зависимости от условий.',
            'raise-shield': 'Включает защиту',
            'fixed-damage': 'Урезание урона',
            'draw-card': 'Добор карт за определенное условие'
        };

        const abilities = (Data as AbilityType[]).map((item) => {
            item.effect_description = effects[item.effect];

            return item;
        });

        const addFavorite = (key: string) => {
            if (favorites.value.some(_key => _key === key)) {
                favorites.value = favorites.value.filter(_key => _key !== key);
            } else {
                favorites.value.push(key);
            }

            favorites.value = favorites.value.filter((v, i, a) => a.indexOf(v) === i);

            cookies.set('favorites', favorites.value);
        }


        watch(() => favorites.value, () => {
            console.log('test');
            try {
                favoriteItems.value = favorites.value.map((key: string) => {
                    const item = abilities.find(item => item.key === key);

                    if (item === undefined) {
                        throw new Error('Not found item');
                    }

                    return item;
                });
            } catch (e) {
                cookies.remove('favorites');
            }
        });

        onMounted(() => {
            if (cookies.get('favorites') !== undefined) {
                favorites.value = cookies.get('favorites');
            }
        });


        return {abilities, addFavorite, effects, favoriteItems, favorites};
    }
});
